	module.exports = {"data":{"post":{"html":"<p>Spiegel Online is reporting that Germany's Springer Verlag, Europe's largest newspaper publisher (Bild, Die Welt) <a href=\"http://www.spiegel.de/wirtschaft/0,1518,291711,00.html\" title=\"Pressebericht: Springer-Konzern will für Daily Telegraph bieten - Wirtschaft - SPIEGEL ONLINE\">will make an offer</a> to buy the Daily Telegraph, Great Britain's largest non-tabloid paper. I guess if they can handle Rover and Mini going German, then they can handle a German newspaper, although it would be more fun to see Springer take over a British tabloid, just to see if it would keep printing <a href=\"http://www.searchlightmagazine.com/stories/storybattlingforthegutter.htm\" title=\"Battling for the gutter\">anti-German headlines</a>.</p>","fields":{"title":"The Spinger Telegraph?","date":"21 Mar 2004","url":"https://papascott.de/2004-03-21-the-spinger-telegraph/"}}},"pathContext":{"slug":"/2004-03-21-the-spinger-telegraph/","prev":{"fields":{"title":"Markdown","date":"21 Mar 2004","slug":"/2004-03-21-markdown/"}},"next":{"fields":{"title":"Ublog Apart","date":"22 Mar 2004","slug":"/2004-03-22-ublog-apart/"}}}}

